import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import MainView from '@/components/MainView'
import TestMap from '@/components/TestMap'

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      name: 'TestMap',
      component: TestMap
    },
    {
          path: '/',
          name: 'HelloWorld',
          component: HelloWorld
        },
    {
      path: '/Map',
      name: 'MainView',
      component: MainView
    }
  ]
})



