<template>
  <el-container>
    <el-aside width="200px">
      <el-menu
        default-active="2"
        class="el-menu-vertical-demo"
        background-color="#545c64"
        @select="select"
      >
        <el-submenu index="1">
          <template slot="title">
            <i class="el-icon-location"></i>
            <span>导航一</span>
          </template>
          <el-menu-item-group>
            <template slot="title">分组一</template>
            <el-menu-item index="1-1">选项1</el-menu-item>
            <el-menu-item index="1-2">选项2</el-menu-item>
          </el-menu-item-group>
          <el-menu-item-group title="分组2">
            <el-menu-item index="1-3">选项3</el-menu-item>
          </el-menu-item-group>
          <el-submenu index="1-4">
            <template slot="title">选项4</template>
            <el-menu-item index="1-4-1">选项1</el-menu-item>
          </el-submenu>
        </el-submenu>
        <el-menu-item index="2">
          <i class="el-icon-menu"></i>
          <span slot="title">增删查改</span>
        </el-menu-item>
        <el-menu-item index="3" disabled>
          <i class="el-icon-document"></i>
          <span slot="title">导航三</span>
        </el-menu-item>
        <el-menu-item index="4">
          <i class="el-icon-setting"></i>
          <span slot="title">热力图</span>
        </el-menu-item>
      </el-menu>
    </el-aside>
    <el-main>
      <div id="map">

      </div>
      <el-card class="box-card info" v-show="showInfo">
        <div slot="header" class="clearfix">
          <span>种植结构信息</span>
          <el-button style="float: right; padding: 3px 0" icon="el-icon-close" type="text" @click="click"></el-button>
        </div>
        <el-row :gutter="20">
          <el-col :span="4">id</el-col>
          <el-col :span="16">{{id}}
            <div class="grid-content bg-purple"></div>
          </el-col>
        </el-row>
        <el-row :gutter="20">
          <el-col :span="4">qhmc</el-col>
          <el-col :span="16">{{qhmc}}
            <div class="grid-content bg-purple"></div>
          </el-col>
        </el-row>
        <el-row :gutter="20">
          <el-col :span="4">zwmc</el-col>
          <el-col :span="16">{{zwmc}}
            <div class="grid-content bg-purple"></div>
          </el-col>
        </el-row>
      </el-card>

      <el-dialog title="人员信息" :visible.sync="dialogTableVisible" width="70%">
        <el-table :data="tableData">
          <el-table-column property="id" label="id"></el-table-column>
          <el-table-column property="name" label="姓名"></el-table-column>
          <el-table-column property="year" label="年龄"></el-table-column>
          <el-table-column
            align="right" width="200">
            <template slot="header" slot-scope="scope">
              <el-button
                size="mini"
                @click="queryUserVisible=true"
                icon="el-icon-search">
              </el-button>
              <el-button
                size="mini"
                @click="addUserVisible=true"
                icon="el-icon-plus">
              </el-button>
            </template>
            <template slot-scope="scope">
              <el-button
                size="mini"
                @click="handleEdit(scope.$index, scope.row)">Edit
              </el-button>
              <el-button
                size="mini"
                type="danger"
                @click="handleDelete(scope.$index, scope.row)">Delete
              </el-button>
            </template>
          </el-table-column>
        </el-table>
        <el-dialog
          width="30%"
          title="增加用户"
          :visible.sync="addUserVisible"
          append-to-body>
          <el-form :model="form">
            <el-form-item label="姓名">
              <el-input v-model="form.name" placeholder="请输入姓名" autocomplete="off"/>
            </el-form-item>
            <el-form-item label="年龄">
              <el-input v-model="form.year" type="number" placeholder="请输入年龄"></el-input>
            </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button @click="addUserVisible = false">取 消</el-button>
            <el-button type="primary" @click="addUserFinal">确 定</el-button>
          </div>
        </el-dialog>
        <el-dialog
          width="30%"
          title="修改用户"
          :visible.sync="editUserVisible"
          append-to-body>
          <el-form :model="form">
            <el-form-item label="姓名">
              <el-input v-model="form.name" placeholder="请输入姓名" autocomplete="off"/>
            </el-form-item>
            <el-form-item label="年龄">
              <el-input v-model="form.year" type="number" placeholder="请输入年龄"></el-input>
            </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button @click="editUserVisible = false">取 消</el-button>
            <el-button type="primary" @click="editUserFinal">确 定</el-button>
          </div>
        </el-dialog>
        <el-dialog
          width="30%"
          title="查询用户"
          :visible.sync="queryUserVisible"
          append-to-body>
          <el-form :model="form">
            <el-form-item label="id">
              <el-input v-model="form.id" type="number" placeholder="请输入id"></el-input>
            </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button @click="queryUserVisible = false">取 消</el-button>
            <el-button type="primary" @click="queryUserFinal">确 定</el-button>
          </div>
        </el-dialog>

      </el-dialog>
    </el-main>
    <coordinateText :lat="lat" :lng="lng"></coordinateText>
  </el-container>

</template>

<script>
  import mapboxgl from 'mapbox-gl';
  import axios from 'axios';
  import coordinateText from './CoordinateText'


  export default {
    components: {coordinateText},
    name: "MainView",
    data() {
      return {
        map: null,
        hasLoaded: false,
        showInfo: false,
        id: null,
        qhmc: null,
        zwmc: null,
        dialogTableVisible: false,
        addUserVisible: false,
        editUserVisible: false,
        queryUserVisible: false,
        searchText: "",//搜索文字
        tableData: [],//表格数据
        form: {
          name: "",
          year: "",
          id: 0
        },
        lng:0,
        lat:0
      }
    },
    mounted() {
      mapboxgl.accessToken = 'pk.eyJ1IjoiaGFtYnVnZXJkZXZlbG9wIiwiYSI6ImNqNXJtZjF4ZzB3em4yd21pZmVqbHlleDAifQ.I9eqVjtotz7jaU7XcJm9pQ';
      this.map = new mapboxgl.Map({
        container: 'map',
        style: 'http://gykj123.cn:9021/api/v1/styles/gykj/B1Ne-foZGt7',
        center: [114.08316983, 33.615693],
        zoom: 7
      });
      const that = this;
      this.map.on('style.load', function () {
        //添加高亮图层
        if (!that.hasLoaded) {
          that.hasLoaded = true;
          that.map.addLayer({
            "id": "种植结构高亮",
            "source": "dt_zzjg",
            "type": "line",
            "minzoom": 1,
            "maxzoom": 23,
            "layout": {
              "visibility": "visible"
            },
            "paint": {
              "line-color": "#00ffff",
              "line-width": 5
            },
            "source-layer": "dt_zzjg",
            "metadata": {
              "mapbox:group": "04687df45bbae"
            },
            "visibility": "none",
            "filter": [
              "in",
              "objectid",
              ""
            ]
          }); // Place polygon under these labels.
        }
        that.addControl();

        that.map.on('mousemove',function (e) {
          that.lng=e.lngLat.lng;
          that.lat=e.lngLat.lat;
        });
        //点击查询
        that.map.on('click', "种植结构", function (e) {
          console.log(e);
          let bbox = [[e.point.x, e.point.y], [e.point.x, e.point.y]];
          let features = that.map.queryRenderedFeatures(bbox, {layers: ['种植结构']});
          if (features.length > 0) {
            let filter = features.reduce(function (memo, feature) {
              memo.push(feature.properties.objectid);
              return memo;
            }, ['in', 'objectid']);
            let coordinates = features[0].geometry.coordinates[0];
            let lngMin = coordinates[0][0];
            let lngMax = coordinates[0][0];
            let latMin = coordinates[0][1];
            let latMax = coordinates[0][1];
            for (let i = 1; i < coordinates.length; i++) {
              if (lngMin > coordinates[i][0]) {
                lngMin = coordinates[i][0]
              } else if (lngMax < coordinates[i][0]) {
                lngMax = coordinates[i][0]
              }
              if (latMin > coordinates[i][1]) {
                latMin = coordinates[i][1]
              } else if (lngMax < coordinates[i][1]) {
                latMax = coordinates[i][1]
              }
            }

            that.map.fitBounds(new mapboxgl.LngLatBounds([lngMin, latMin], [lngMax, latMax]), {
              padding: 60
            });


            that.showInfo = true;
            that.id = features[0].properties.objectid;
            that.qhmc = features[0].properties.qhmc;
            that.zwmc = features[0].properties.zwmc;
            that.map.setFilter("种植结构高亮", filter);
          } else {
            that.showInfo = false;
          }
        });

      });


      // let userAgent = navigator.userAgent; //取得浏览器的userAgent字符串
      // if (userAgent.indexOf("Chrome") > -1) {
      //   //谷歌浏览器异常修复
      //   let canvas = document.querySelectorAll("canvas");
      //   canvas[0].style.left = 0;
      // }
    },
    methods: {
      /**
       * 添加控件
       */
      addControl: function () {
        let scale = new mapboxgl.ScaleControl({
          maxWidth: 80,
          unit: 'imperial'
        });
        this.map.addControl(scale);
        this.map.addControl(new mapboxgl.FullscreenControl({container: document.querySelector('body')}));
      },
      click: function () {
        this.showInfo = false;
        this.map.setFilter("种植结构高亮", ["in", "objectid", ""]);
      },
      select: function (index, indexPath) {
        if (index == 2) {
          const that = this;
          this.dialogTableVisible = true;
          this.getAllUser();
        }else if(index==4){
          this.addHeatMap();
        }
      },
      handleEdit: function (index, row) {
        this.form = row;
        this.editUserVisible = true;
      },
      handleDelete: function (index, row) {
        this.deleteById(row.id)
      },
      deleteById: function (id) {
        const that = this;
        axios.delete("http://localhost:8089/try/user/" + id).then(function (response) {
          that.getAllUser();
          if (response.data && response.data == "success") {
            that.$message('删除成功');
          } else {
            that.$message('删除失败');
          }
        }).catch(function (error) {
          that.$message('删除失败');
        })
      },
      /**
       * 获取所有用户信息
       */
      getAllUser: function () {
        const that = this;
        axios.get('http://localhost:8090/try/user')
          .then(function (response) {
            if (response.data && response.data.length > 0) {
              that.tableData = response.data;
            }
          })
          .catch(function (error) {
            console.log(error);
          });
      },

      addUserFinal: function () {
        const that = this;
        axios.post("http://localhost:8089/try/user?name=" + this.form.name + "&year=" + this.form.year).then(function (response) {
          console.log(response);
          that.getAllUser();
          if (response.data && response.data == "success") {
            that.$message('新增成功');
          } else {
            that.$message('新增失败');
          }
        }).catch(function (error) {
          that.$message('新增失败');
        });
        this.addUserVisible = false
      },

      editUserFinal: function () {
        const that = this;
        axios.put("http://localhost:8089/try/user?id=" + this.form.id + "&name=" + this.form.name + "&year=" + this.form.year).then(function (response) {
          that.getAllUser();
          if (response.data && response.data == "success") {
            that.$message('修改成功');
          } else {
            that.$message('修改失败');
          }
        }).catch(function (error) {
          that.$message('修改失败');
        });
        this.editUserVisible = false
      },

      queryUserFinal: function () {
        const that = this;
        axios.get("http://localhost:8089/try/user/" + this.form.id).then(function (response) {
          that.getAllUser();
          if (response.data.length == 0) {
            that.$message('没有查询到符合条件的数据');
          } else {
            that.$message('查询成功-->姓名：' + response.data[0].name + "年龄：" + response.data[0].year);
          }
        }).catch(function (error) {
          that.$message('查询失败');
        });
        this.queryUserVisible = false
      },
      addHeatMap: function () {
        this.map.addSource('earthquakes', {
          "type": "geojson",
          "data": "https://docs.mapbox.com/mapbox-gl-js/assets/earthquakes.geojson"
        });

        this.map.addLayer({
          "id": "earthquakes-heat",
          "type": "heatmap",
          "source": "earthquakes",
          "maxzoom": 9,
          "paint": {
            "heatmap-weight": [
              "interpolate",
              ["linear"],
              ["get", "mag"],
              0, 0,
              6, 1
            ],

            "heatmap-intensity": [
              "interpolate",
              ["linear"],
              ["zoom"],
              0, 1,
              9, 3
            ],

            "heatmap-color": [
              "interpolate",
              ["linear"],
              ["heatmap-density"],
              0, "rgba(33,102,172,0)",
              0.2, "rgb(103,169,207)",
              0.4, "rgb(209,229,240)",
              0.6, "rgb(253,219,199)",
              0.8, "rgb(239,138,98)",
              1, "rgb(178,24,43)"
            ],
            "heatmap-radius": [
              "interpolate",
              ["linear"],
              ["zoom"],
              0, 2,
              9, 20
            ],
            "heatmap-opacity": [
              "interpolate",
              ["linear"],
              ["zoom"],
              7, 1,
              9, 0
            ],
          }
        });
      }
    }
  }
</script>

<style scoped>
  .el-container, #map, ul {
    height: 100%;
  }

  .el-main {
    padding: 0;
    margin: 0;
  }

  #map {
    z-index: 1;
    width: calc(100vw - 200px);
  }

  .info {
    position: absolute;
    z-index: 100;
    left: 200px;
    top: 0;
    width: 300px;
  }
</style>



