<template>
  <div class="parent">
    <div class="chart">
      <v-chart style="height: 100%;height: 100%" theme="ovilia-green" :options="getStyle1"/>
    </div>
    <div class="chart">
      <v-chart style="height: 100%;height: 100%" theme="ovilia-green" :options="getStyle2"/>
    </div>
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <span>测试系统登录</span>
      </div>
      <el-input v-model="name" placeholder="请输入用户名"></el-input>
      <el-input v-model="pass" placeholder="请输入用密码"></el-input>
      <el-button type="primary" v-on:click="login">登录</el-button>
    </el-card>
  </div>
</template>

<script>


  export default {
    name: 'HelloWorld',
    data() {
      return {
        name: "",
        pass: "",
        line: {
          legend: { // 图例组件颜色配置
            data: [
              {name: '一二三四五六 ', icon: 'circle', textStyle: {color: '#fff'}},
              {name: '一二三四五六  ', icon: 'circle', textStyle: {color: '#fff'}},
              {name: '一二三四五六   ', icon: 'circle', textStyle: {color: '#fff'}},
              {name: '一二三四五六    ', icon: 'circle', textStyle: {color: '#fff'}}
            ]
          },
          grid: { // 网格
            left: '-2.3%',
            right: '1.6%',
            bottom: '10%',
            containLabel: true
          },
          toolbox: {
            feature: {
              saveAsImage: {}
            }
          },
          xAxis: {
            splitLine: { // 保留网格线
              show: true,
              lineStyle: {
                color: '#666'
              }
            },
            splitArea: { // 去除网格区域
              show: false,
              lineStyle: {
                color: '#666'
              }
            },
            type: 'category',
            boundaryGap: false,
            axisLine: { // 坐标轴
              lineStyle: {
                color: '#ccc'
              }
            },
            axisTick: { // 坐标轴刻度设置
              inside: true,
              interval: 0
            },
            axisLabel: {
              interval: 0,
              fontSize: 8
            },
            data: ['12:00', '13:00', '14:00', '15:00', '16:00', '17:00', '18:00', '19:00', '20:00', '21:00', '22:00', '23:00', '24:00', '01:00', '02:00', '03:00', '04:00', '05:00', '']

          },
          yAxis: {
            min: 1000,
            splitLine: { // 保留网格线
              show: true,
              lineStyle: {
                color: '#666'
              }
            },
            splitArea: { // 去除网格区域
              show: false,
              lineStyle: {
                color: '#666'
              }
            },
            type: 'value',
            axisLabel: {
              interval: '0',
              fontSize: 8.5
            },
            axisLine: {
              lineStyle: {
                color: '#ccc'
              }
            },
            axisTick: {
              inside: true
            },
            data: [1000, 2000, 3000, 4000, 5000, 6000, 7000, 8000, 9000]
          },
          series: [
            {
              name: '一二三四五六 ',
              type: 'line',
              stack: '总量',
              symbol: 'circle',
              symbolSize: 7,
              itemStyle: {
                normal: {
                  color: '#00ff7e',
                  lineStyle: {
                    color: '#00ff7e'
                  }
                }
              },
              data: [1700, 1720, 1010, 1340, 900, 2300, 2100, 1500]
            },
            {
              name: '一二三四五六  ',
              type: 'line',
              stack: '总量',
              symbol: 'circle',
              symbolSize: 7,
              itemStyle: {
                normal: {
                  color: '#0060ff',
                  lineStyle: {
                    color: '#0060ff'
                  }
                }
              },
              data: [1200, 1820, 1910, 2304, 2090, 3040, 3010, 3000]
            },
            {
              name: '一二三四五六   ',
              type: 'line',
              stack: '总量',
              symbol: 'circle',
              symbolSize: 7,
              itemStyle: {
                normal: {
                  color: '#00ccff',
                  lineStyle: {
                    color: '#00ccff'
                  }
                }
              },
              data: [1500, 2320, 2010, 1054, 1900, 3300, 3100, 3500]
            },
            {
              name: '一二三四五六    ',
              type: 'line',
              stack: '总量',
              symbol: 'circle',
              symbolSize: 7,
              itemStyle: {
                normal: {
                  color: '#ffae00',
                  lineStyle: {
                    color: '#ffae00'
                  }
                }
              },
              data: [1200, 3320, 301, 3304, 3900, 330, 3200, 3050]
            }
          ]
        },
        bar:{
          color: ['#3398DB'],
          tooltip : {
            trigger: 'axis',
            axisPointer : {            // 坐标轴指示器，坐标轴触发有效
              type : 'shadow'        // 默认为直线，可选为：'line' | 'shadow'
            }
          },
          grid: {
            left: '3%',
            right: '4%',
            bottom: '3%',
            containLabel: true
          },
          xAxis : [
            {
              type : 'category',
              data : ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun'],
              axisTick: {
                alignWithLabel: true
              }
            }
          ],
          yAxis : [
            {
              type : 'value'
            }
          ],
          series : [
            {
              name:'直接访问',
              type:'bar',
              barWidth: '60%',
              data:[10, 52, 200, 334, 390, 330, 220]
            }
          ]
        }
      }
    },
    mounted(){
      const that=this;
      setInterval(function () {
        let datas=[
          {
            name:'直接访问',
            type:'bar',
            barWidth: '60%',
            data:that.getRandomData()
          }
        ];
        that.$set(that.bar,"series", datas)
      },2000);
    },
    methods: {
      login: function () {
        this.$router.push({path: '/Map'})
      },
      getRandomData:function () {
        let array=[];
        for (let i = 0; i <7 ; i++) {
          array.push(this.getRandom())
        }
         return array
      },
      getRandom:function () {
        return Math.floor(Math.random()*400)
      }
    },
    computed:{
      getStyle1:function () {
        return this.bar
      },
      getStyle2:function () {
        return this.line
      }
    }
  }
</script>

<style scoped>
  .el-input, .el-button {
    margin-top: 5px;
  }

  .text {
    font-size: 14px;
  }

  .item {
    margin-bottom: 18px;
  }

  .clearfix:before,
  .clearfix:after {
    display: table;
    content: "";
  }

  .clearfix:after {
    clear: both
  }

  .box-card {
    width: 480px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }

  .chart {
    width: 300px;
    height: 300px;
  }
  .echarts{
    width: 100%;
  }

  .parent {
    width: 100%;
    height: 100%;
  }
</style>



